/* Header */

.navBar_top{
   background-color: rgb(238,238,238);
   overflow: hidden;
   padding: 10px;
}
.navBar_top select, option{
    border: none;
    float: left;
    color: #888;
    text-align: center; 
    background-color: rgb(238,238,238);
}
.navBar_top button:hover{
    color: black;
}
.navbar_top_options{
    float: right;
    list-style-type: none;
    margin: 0;
    padding: 0;
}
.navbar_top_options li{
    display: inline;
    padding: 8px;
}
.navbar_top_options li a{
    font-size: 0.84em;
    text-align: center;
    text-decoration: none;
    color: #888;
}
.navbar_top_options li a:hover{
    color: black;
}

.header { 
    clear: both;
}
.header{
    margin-left: auto;
    margin-right: auto;
    max-width: 1200px;
}
.navBar{
    padding: 10px;
    height: 70px;
}
.company_name{
    text-decoration: none;
    color: rgb(34,155,202);
    font-family: "Open Sans",sans-serif;
    text-transform: uppercase;
    font-size: 30px;
    /* display: inline; */
    float: left;
    max-width: 400px;
    font-weight: 170;
}
.navbar_options{
    list-style-type: none;
    margin: 15px;
    padding: 10px;
    background-color: rgb(34,155,202);
    border-radius: 10px;
}
.navbar_options li{
    display: inline;
    padding: 10px;
    color: white;
}
.navbar_options li:hover{
    background-color: rgb(12, 121, 164);
}
.cart_btn{
    float: right;
    width: 200px;
    height: 40px;
    border-radius: 5px;
    line-height: 20px;
    padding: 5px;
    background-color: rgb(61,61,61);
    border-radius: 5px;
    color: white;
    font-size: 13px;
}
.cart_btn:hover{
    background-color: black;
}
.search_nav input{
    width: 350px;
    height: 40px;
    border-radius: 5px 0px 0px 5px;
    line-height: 20px;
    background-color: rgba(255, 255, 255, 0.584);
    border-color: rgb(238,238,238);
    /* display: inline; */
    float: left;
}
.search_nav button{
    /* display: inline; */
    width: 50px;
    height: 40px;
    border-radius: 0px 5px 5px 0px;
    line-height: 20px;
    background-color: #dedede;
    border: none;
    float: left;
}
@media (max-width: 1100px) {
    .search_nav input{
      width: 250px;
    }
    .search_nav button{
      width: 40px;
    }
    .cart_btn{
        width: 150px;
    }
    .company_name{
        font-size: 25px;
        max-width: 280px;
        font-weight: 150;
    }
    .cart_btn span{
        display: none;
    }
  }
  @media (max-width: 750px) {
    .cart_btn{
        display: none;
    }
    .search_nav{
        float: right;
    }
    .search_nav input{
        width: 200px;
      }
    .description{
        display: none;
    }

  }
  @media (max-width: 600px) {
    .company_name{
        font-size: 20px;
        max-width: 250px;
        font-weight: 120;
    }
    .navbar_top_options span{
        display: none;
    }
    .search_nav{
        /* display: both; */
        float: right;
        margin-top: -10px;
    }
    .search_nav input{
        width: 100px;
      }
      .search_nav button{
        width: 30px;
      }
    .navbar_options{
        margin-top: 5px;
        padding: 8px;
        background-color: rgb(34,155,202);
        border-radius: 10px;
    }
    .navbar_options li{
        display: block;
        padding: 4px;
        color: white;
        text-align: center;
    }
  }

/* Main */
.main, .contactus, .aboutUs, .login, .register, .productInfo, .emptyStatus, .wishlist, .compare, .paymentPage, .totalPrice, .termsCondition, .privacyPolicy,.orderHistory{
    margin-left: auto;
    margin-right: auto;
    max-width: 1200px;
}
.banner_img img{
    max-width: 100%;
    max-height: 400px;
}
.pageChange{
    clear: both;
    margin-left: 40px;
}

.pageChange button{
    border: 1px solid #b2b2b2;
    background-color: rgb(238,238,238);
    /* color: #888;  */
    padding: 5px; 
}
.card{
/* Flex will make in row */
    display: flex; 
    flex-direction: column;
    /* width: 300px; */
    border: 1px solid #b2b2b2;
    padding: 15px;
    margin: 5px;
    /*  */
    align-items: center;
    justify-content: flex-end;
    width: 280px;
    height: 400px;
    margin-bottom: 0;
    float: left;
    margin-left: 50px;
    margin-bottom: 50px;
}
.card img{
    width: 160px;
    object-fit: contain;
    margin-left: auto;
}
.card p{
    font-weight: 200;
}
.abx{
    display: flex;
    margin-top: 15px;
}
.abx button{
    border: 1px solid #b2b2b2;
    background-color: rgb(238,238,238);
    color: #888; 
    padding: 10px; 
    margin-top: 0;
    width: 104px;
}
/* .card_buttons{
    margin-left: 5px;
    display: flex;
}
.card_buttons button{
    border: 1px solid #b2b2b2;
    background-color: rgb(238,238,238);
    color: #888; 
    padding: 10px; 
    margin-top: 0;
    width: 104px;
} */

.card h4{
    color: rgb(34,155,202);
}
.card p{
    margin: 0;
}

.card button:hover{
    background-color: #888; 
    color: white;   
}


.partition{
    border-top: 0.2px dotted white;
    margin: 10px;
}
/* Footer */

.footer{
    background-color: rgb(61,61,61);
    overflow: hidden;
    clear: both;
}

.footer_columns {
    margin-left: auto;
    margin-right: auto;
    width: 1200px;
  }
.column {
    float: left;
    width: 20%;
    padding: 25px;
    /* height: 200px; */
  }

@media (max-width: 1100px) {
    .footer_columns {
        width: 780px;
      }
    .column{
        width: 15%;
        padding: 30px;
    }
}
@media (max-width: 750px) {
    .footer_columns {
        width: 580px;
      }
    .column {
        width: 20%;
        padding: 5px;
        /* height: 200px; */
      }
}
@media (max-width: 500px) {
    .footer{
        padding: 4px;
    }
    .footer_columns {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        width: 100%;
      }
    .column {
        width: 45%;
        padding: 5px;
        margin-bottom: 10px;
      }
      .column:nth-child(2n+1) {
        margin-right: 10px;
      }
    
}
.row:after {
    content: "";
    display: table;
    clear: both;
}
.column ul{
    font-weight: bold;
    padding: 0;
    color: white;
}
.column li{
    list-style-type: none;
    text-align: left;
    padding: 2px;
    font-size: 0.8em;
    color: whitesmoke;
}
.copy_footer{
    clear: both;
    color: #888;
    margin-left: auto;
    margin-right: auto;
    width: 1200px;
    font-size: small;
}
.copy_footer hr{
    width: 1200px;
}

.card button{
    width: 90px;
    height: 30px;
    border: 1px solid #888;
    border-radius: 10px;
    padding: 2px;
    margin-right: 5px;
}

/* Checkout */
.checkout{
    margin-left: auto;
    margin-right: auto;
    width: 1200px;
    display: flex;
}
.checkout_right h1{
    font-weight: 200;
}
.checkout_left h1{
    font-weight: 200;
    width: 800px;
}
.checkout_left p{
    padding: 8px;
}
.checkout_left{
    width: 80%;
}

/* Subtotal */
.subtotal{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 10px;
    border: 1px solid #888;
    border-radius: 5px;
    max-width: 250px;
    height: 220px;
    margin-bottom: 10px;
    background-color: #f3f3f3;
}
.subtotal button{
    background-color: rgb(34,155,202);
    color: white;
    border: 1px solid rgb(34,155,202);
    border-radius: 5px;
    padding: 10px;
    text-shadow: rgb(34,155,202);
    margin-top: -10px;
    margin-bottom: 10px;
}
.subtotal button:hover{
    background-color: rgb(12, 121, 164);
    color: white;   
    border: 1px solid rgb(12, 121, 164);
}

/* About Us */

.aboutUs li,p{
    line-height: 1.8em;
    font-weight: 100;
}

/* Login */
.login{
    display: flex;
}
.login_left, .login_right{
    width: 500px;
    padding: 10px;
    border: 1px solid #b2b2b2;
    background-color: rgb(238,238,238);
    border-radius: 10px;
    margin: 70px;
}
.login_left{
    height: 200px;
}
.login_right{
    padding-left: 20px;
    width: 450px;
    height: 280px;
}
.login_left h2, .login_right h2{
    font-weight: 400;
    margin-bottom: 0;
}
.login_left small, .login_right small{
    font-weight: 500;
    color: #888;
}
.login_left p, .login_right p{
    line-height: 18px;
    font-size: 0.8em;
}
.login_left button, .login_right button{
    background-color: rgb(34,155,202);
    color: white;
    border: 1px solid rgb(34,155,202);
    border-radius: 5px;
    padding: 10px;
    text-shadow: rgb(34,155,202);
}
.login_right p{
    margin-bottom: 0;
}
.login_right input{
    border-radius: 4px;
    border: 1px solid #b2b2b2;
    padding: 5px;
}
.login_right button{
    margin-top: 10px;
}
.login_right button:hover{
        background-color: rgb(12, 121, 164);
        color: white;   
        border: 1px solid rgb(12, 121, 164);
}
.login_left button:hover{
        background-color: rgb(12, 121, 164);
        color: white;   
        border: 1px solid rgb(12, 121, 164);
}

/* Contact Us */
.location{
    border: 1px solid #cac8c8;
    border-radius: 5px;
    display: flex;
    margin: 5px;
}
.location_left{
    width: 300px;
    padding: 10px;
    margin: 10px;
}
.location_right{
    width: 300px;
    padding: 10px;
    margin: 10px;
}
.location_right p:first-child, .location_left p:first-child{
    font-weight: 400;
    font-size: 1.2em;
}
.location_right p, .location_left p{
    margin: 0;
    padding: 0;
    line-height: 1.5em;
}
.contactus hr{
    border: 0.01px solid #eeecec;
}
.contactus form{
    float: right;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}
.contactus form label{
    padding: 10px;
    vertical-align: top;
}
.contactus form button{
    background-color: rgb(34,155,202);
    color: white;
    border: 1px solid rgb(34,155,202);
    border-radius: 5px;
    padding: 10px;
    text-shadow: rgb(34,155,202);
    margin-top: -10px;
    margin-bottom: 10px;
}
.contactus form input{
    width: 800px;
    height: 25px;
   border: 2px solid rgb(208,208,208);
   border-radius: 5px;
}
.contactus form input:focus{
   border: 2px solid rgb(84, 162, 193);
   border-color: rgb(34,155,202);
}
.contactus form textarea{
    width: 800px;
    height: 150px;
    border: 2px solid rgb(208,208,208);
    border-radius: 5px;
    margin-bottom: 0;
}

/* Register */
.register{
    border: 1px solid #b2b2b2;
    background-color: rgb(238,238,238);
    padding: 10px;
    border-radius: 5px;
    max-width: 400px;
    max-height: 800px;
    margin-bottom: 5px;
    
}
.register p{
    margin: 0;
}
.register strong, h1{
    margin: 0;
    padding: 0;
}
.register p{
    padding-top: 10px;
}
.registerBtn button{
    background-color: rgb(34,155,202);
    color: white;
    border: 1px solid rgb(34,155,202);
    border-radius: 5px;
    padding: 10px;
    text-shadow: rgb(34,155,202);
    margin-top: 10px;
}
/*  */

h5::after{
    content: "*Please exit checkout first";
    
}
h5{
    margin-left: auto;
    margin-right: auto;
    width: 200px;
    color: red;
    font-weight: 100;
    margin-top: 0;
}

.my-Link{
    text-decoration: none;
}
.my-Link img{
}



/* Product Info for indicidual */
.productInfo{
    display: flex;
}
.productInfo_left img{
    display: block;
    width:250px;
    padding: 20px;
    height: 180px;
    border: 1px solid #ddd;
    border-radius: 4px;
    padding-left: 200px;
    padding-right: 200px;
}
.productInfo_left, .productInfo_right{
    padding: 10px;
    text-align: center;
}
.productInfo_left p, .productInfo_right p{
    color: #555;
}
.productInfo_right p{
    text-align: left;
}
.productInfo_left{
    padding-right: 40px;
}
.productInfo_right input{
    width: 80%;
}
.productInfo_right button{
    width: 60%;
    background-color: rgb(34,155,202);
    color: white;
    border: 1px solid rgb(34,155,202);
    border-radius: 5px;
    padding: 10px;
    text-shadow: rgb(34,155,202);
}


/* cardCheckout */
.cardCheckout, .cardWishList{
    /* Flex will make in row */
    display: flex;
    flex-direction: row;
    border: 1px solid #b2b2b2;
    padding: 15px;
    margin: 5px;
    /*  */
    align-items: center;
    /* justify-content: flex-end; */
    width: 400px;
    height: 150px;
    margin-bottom: 0;
    float: left;
    margin-left: 40px;
}

/* width: 300px; */
.cardCheckout img, .cardWishList img{
    width: 70px;
    object-fit: contain;
    }
.cardCheckout p, .cardWishList p{
    font-weight:7;
}
.cardCheckout button{
    margin: 6px 0 5px auto;
    background-color: #da4f49;
    color: white;
    border: 1px solid #da4f49;
    border-radius: 5px;
    padding: 7px;
    text-shadow: #da4f49;
}
@media (max-width: 600px) {
    .cardCheckout img, .cardWishList img{
        width: 50px;
        }
}
.removeWishList{
    margin: 5px 0 5px auto;
    background-color: #da4f49;
    color: white;
    border: 1px solid #da4f49;
    border-radius: 5px;
    padding: 10px;
    text-shadow: #da4f49;
}
.addCartWish{
    width: 60%;
    background-color: rgb(34,155,202);
    color: white;
    border: 1px solid rgb(34,155,202);
    border-radius: 5px;
    padding: 10px;
    text-shadow: rgb(34,155,202);
}
.guestCheckout{
    margin-left: 6%;
    margin-top: -140px;
    margin-bottom: 50px;
}
.totalPrice{
    width: 200px;
    text-align: center;
    border: 1px solid #b2b2b2;
    background-color: rgb(238,238,238);
    border-radius: 10px;
    padding: 10px;
    margin-bottom: 10px;
}
.totalPrice button{
    width: 50%;
    background-color: rgb(34,155,202);
    color: white;
    border: 1px solid rgb(34,155,202);
    border-radius: 5px;
    padding: 10px;
    text-shadow: rgb(34,155,202);
}
.orderHistory{
    display: flex; 
    flex-direction: column;
}
.orderHistoryProducts img{
    float: left;
    width: 100px;
}